(function(){
  const BTN_OPEN_MENU = document.getElementById("nav-btn-menu");
  const BTN_CLOSE_MENU = document.getElementById("nav-btn-closeMenu");
  const DISPLAYERS = document.getElementsByClassName("displayer");
  const NAV_CONTAINER = document.getElementById("navContainer");
  
  for (const LIST of DISPLAYERS) {
    LIST.addEventListener("click", toggleVisibility);
  }

  BTN_OPEN_MENU.addEventListener("click", showNavBtn);
  BTN_CLOSE_MENU.addEventListener("click", hideNavBtn);
  NAV_CONTAINER.addEventListener("click", hideNavTouchOutside);
  function showNavBtn(){
    NAV_CONTAINER.classList.remove("hidden");
  }
  
  function hideNavBtn(){
    NAV_CONTAINER.classList.add("hidden");
  }

  function hideNavTouchOutside(event){
    if(event.target === NAV_CONTAINER){
      NAV_CONTAINER.classList.add("hidden");
    }
  }
  
  function toggleVisibility(){
    this.nextElementSibling.classList.toggle("hidden");
    this.firstElementChild.classList.toggle("--active");
  }
})();